import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, BehaviorSubject } from 'rxjs/Rx'
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/toPromise';
import 'rxjs/Rx';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { take } from 'rxjs/operators';
import { Router } from '@angular/router';

@Injectable({
	providedIn: 'root'
})
export class CommonService {

	API_URL = 'http://127.0.0.1:5858/api/'                     // Local


	currentMessage = new BehaviorSubject(null);
	// stripeKey: string = 'pk_test_aD1TFPc4daHbjTiwjYshInjM009TridqIM'
	stripeKey: string = 'sk_live_pqz0475c3Ei5OF1WaRRvVzQC00tDi2CCH9'
	expiredDate: any;
	typeForPayment: any
	constructor(private router: Router, private httpClient: HttpClient, private toastr: ToastrService, private spinner: NgxSpinnerService) {
	}
	public register<T>(requestObj: any): Observable<T> {
		return this.httpClient.post<T>(`${this.API_URL}merchant/signup`, requestObj);
	}
	public login<T>(requestObj: any): Observable<T> {
		return this.httpClient.post<T>(`${this.API_URL}merchant/login`, requestObj);
	}

	public registerEvent<T>(requestObj: any, files: any, food_tags: any, event_tags: any): Observable<T> {
		const headers = new HttpHeaders({ 'accesstoken': JSON.parse(localStorage.getItem('blixr_user')).token });
		var formData: FormData = new FormData();
		formData.append('images', files[0]);
		formData.append('images', files[1]);
		formData.append('images', files[2]);
		formData.append('images', files[3]);
		formData.append('event_category_id', requestObj.event_category_id);
		formData.append('creator_id', requestObj.creator_id);
		formData.append('creator_type', 'merchant');
		formData.append('event_name', requestObj.event_name);
		formData.append('event_location', requestObj.event_location);
		formData.append('event_date', requestObj.event_date);
		formData.append('event_date_end', requestObj.event_date_end);

		formData.append('go_live_date', requestObj.go_live_date);

		formData.append('event_from', requestObj.event_from);
		formData.append('event_to', requestObj.event_to);
		formData.append('total_no_ticket', requestObj.total_no_ticket);
		formData.append('total_price_per_ticket', requestObj.total_price_per_ticket);
		formData.append('discount', requestObj.discount);
		formData.append('event_tags', event_tags);
		formData.append('event_recuring', requestObj.event_recuring);
		formData.append('event_status', requestObj.event_status);
		formData.append('event_latitude', requestObj.event_latitude);
		formData.append('event_longitude', requestObj.event_longitude);
		formData.append('food_tags', food_tags);
		formData.append('event_type', requestObj.event_type);
		formData.append('deleted_id', requestObj.deleted_id);
		formData.append('deleted_id', requestObj.deleted_id);
		formData.append('event_dates', JSON.stringify(requestObj.itemRows))
		formData.append('book_url', requestObj.book_url)
		formData.append('street_address', requestObj.street_address)
		formData.append('is_draft', requestObj.is_draft)
		formData.append('experienceType', requestObj.experienceType)

		// 
		formData.append('event_description', requestObj.event_description);
		if (requestObj.event_id) {
			formData.append('event_id', requestObj.event_id);
		}

		return this.httpClient.post<T>(`${this.API_URL}event/add`, formData, { headers: headers });
	}

	public getEventById<T>(event_id: any): Observable<T> {
		const headers = new HttpHeaders({ 'accesstoken': JSON.parse(localStorage.getItem('blixr_user')).token });
		return this.httpClient.post<T>(`${this.API_URL}event/id`, { event_id: event_id }, { headers: headers });
	}

	public getEventCategory<T>(): Observable<T> {
		const headers = new HttpHeaders({ 'accesstoken': JSON.parse(localStorage.getItem('blixr_user')).token });
		return this.httpClient.post<T>(`${this.API_URL}event/eventcategorylist`, {}, { headers: headers });
	}

	public getEventTag<T>(): Observable<T> {
		const headers = new HttpHeaders({ 'accesstoken': JSON.parse(localStorage.getItem('blixr_user')).token });
		return this.httpClient.get<T>(`${this.API_URL}event/eventTagList`, {});
	}

	public getEventList<T>(): Observable<T> {
		const token = JSON.parse(localStorage.getItem('blixr_user')).token;
		return this.httpClient.post<T>(`${this.API_URL}merchant/event/list`, { token: token });
	}
	postApi(url, data, isHeader): Observable<any> {
		// this.spinner.show();
		if (isHeader == 0) {
			var httpOptions;
			httpOptions = {
				headers: new HttpHeaders({ "Content-Type": "application/json" })
			}

			return this.httpClient.post((this.API_URL + url), data, httpOptions)
		}
		else if (isHeader == 1) {
			var httpOptions;
			httpOptions = {

				headers: new HttpHeaders({
					"accessToken": JSON.parse(localStorage.getItem('blixr_user')).token,
					// "_id": localStorage.adminId,
					/* 	"Content-Type": "application/json" */
				})
			}
			return this.httpClient.post((this.API_URL + url), data, httpOptions)
		}
	}

	getApi(url, isHeader) {

		if (isHeader == 0) {
			var httpOptions;
			httpOptions = {
				headers: new HttpHeaders({ "Content-Type": "application/json" })
			}
			return this.httpClient.get((this.API_URL + url), httpOptions)
		}
		else if (isHeader == 1) {
			// 
			var httpOptions;
			httpOptions = {
				headers: new HttpHeaders({ "accessToken": JSON.parse(localStorage.getItem('blixr_user')).token, "Content-Type": "application/json" })
			}
			return this.httpClient.get((this.API_URL + url), httpOptions)
		}
	}

	showSuccess(msg) {
		this.toastr.success(msg);
	}
	showNotification(title, msg, payload) {
		console.log(title, msg, payload)
		this.toastr.success(title, msg)
			.onTap
			.pipe(take(1))
			.subscribe(() => this.toasterClickedHandler(payload));
	}
	toasterClickedHandler(payload) {
		this.router.navigate(['payment'])
		this.typeForPayment = payload.data['gcm.notification.type']

	}
	showError(msg) {
		this.toastr.error(msg)
	}
	showBug(msg) {
		this.toastr.info(msg)

	}
	showWarningToaster(msg) {
		this.toastr.warning(msg)

	}
	public date = new BehaviorSubject(new Date());
	public experience = new BehaviorSubject('Experience');
	public experienceName = new BehaviorSubject("Experience Name");
	public toggle = new BehaviorSubject(false)

	setExperience(title: string) {
		this.experience.next(title);
	}
	setExperienceName(text: string) {
		this.experienceName.next(text)
	}
	setToggle(val) {
		this.toggle.next(val)
	}

	getDate(val) {
		this.date.next(val)
	}
	showSpinner() {
		this.spinner.show()
	}
	hideSpinner() {
		this.spinner.hide()
	}
	setExpiredDate(val) {
		this.expiredDate = val;
	}

	get windowRef() {
		return window
	}
	getCountryCodeJson() {
		return this.httpClient.get('assets/json/countryCodes.json');
	}
}