import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { CommonService } from './common.service';
import { NgProgress } from 'ngx-progressbar';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'finlock';
  constructor(private activate: ActivatedRoute, private router: Router, public ngProgress: NgProgress, private commonService: CommonService) {
    this.router.events.subscribe(x => {
      if (x instanceof NavigationEnd) {
        window.scroll(0, 0)
        let currUrl = this.router.url.indexOf('?') == -1 ? this.router.url.split('/')[1] : this.router.url.slice(1, this.router.url.indexOf('?'))

        if (!(currUrl == "home")) {
          // localStorage.removeItem('dashvalue')
        }
      }
    })
  }
}
