import { NgModule } from '@angular/core';
import { RouterModule, Routes, CanDeactivate } from '@angular/router';

const routes: Routes = [
  { path: '', loadChildren: './login-tab/login-tab.module#LoginTabModule' },
  { path: 'profile', loadChildren: './profile-tab/admin-profile-tab.module#ProfileTabModule' },
  { path: 'users', loadChildren: './users-tab/users-tab.module#UsersTabModule' },
  { path: 'categories', loadChildren: './feature-category-tab/feature-category-tab.module#FeatureCategoryTabModule' },
  { path: 'courses', loadChildren: './courses-tab/courses-tab.module#CoursesTabModule' },
  { path: 'banners', loadChildren: './banner-tab/banner-tab.module#BannerTabModule' },
];
@NgModule({
  imports: [RouterModule.forRoot(routes, { onSameUrlNavigation: 'reload', useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }



